:root {
  --ff-primary: "Courier New", Courier, monospace;
}

.card-action {
  background-color: var(--clr-accent-500);
  color: var(--clr-primary-200);
  float: right;
  border: none;
  border-radius: 15px;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 100%;
  height: 3.5rem;
  font-size: 1.25em;
  text-decoration: none;
  display: flex;
}

.card-action:hover {
  background-color: var(--clr-neutral-800);
}

.card-action img {
  height: 1.5em;
  margin-right: 1rem;
}

.card-container {
  width: 90%;
  margin: auto;
}

.card-left, .card-right {
  background-color: var(--clr-primary-300);
  color: var(--clr-neutral-900);
  flex-direction: column;
  height: min-content;
  margin-top: 2rem;
  padding: 1rem;
  display: flex;
  overflow: hidden;
}

.card-left {
  border-right: 10px solid var(--clr-accent-500);
  border-radius: 50px 0 0 50px;
}

.card-right {
  border-left: 10px solid var(--clr-accent-500);
  border-radius: 0 50px 50px 0;
}

.card-image {
  grid-area: image;
}

.card-image img {
  object-fit: cover;
  object-position: center;
  width: min(100%, 20rem);
  margin: auto;
  display: block;
}

.card-details {
  grid-area: details;
  padding: 1rem;
}

.card-title:after {
  border-bottom: 1px solid var(--clr-neutral-900);
  content: "";
  width: 100%;
  display: block;
}

@media (min-width: 860px) {
  .card-container {
    flex-direction: column;
    justify-content: center;
    height: 75vh;
    display: flex;
  }

  .card-left {
    grid-template-columns: 25% 75%;
    grid-template-areas: "image details";
    display: grid;
  }

  .card-right {
    grid-template-columns: 75% 25%;
    grid-template-areas: "details image";
    display: grid;
  }

  .card-column {
    grid-template-columns: 50% 50%;
    display: grid;
  }

  .card-container {
    width: max(50%, 50rem);
  }
}

body {
  background-color: var(--clr-neutral-200);
  color: var(--clr-neutral-800);
  font-family: var(--ff-primary);
}

.app {
  flex-direction: row;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.nav {
  background-color: var(--clr-neutral-200);
  flex-direction: row;
  width: 100%;
  height: 100%;
  padding: 3rem;
  transition: all .5s cubic-bezier(.075, .82, .165, 1);
  display: flex;
  position: absolute;
}

.nav.closed {
  margin-left: -100%;
}

.nav-icon {
  z-index: 1;
  margin: 1rem;
  position: absolute;
}

.nav-icon svg {
  color: var(--clr-neutral-800);
}

.nav-icon.closed svg {
  color: var(--clr-neutral-200);
}

.nav-icon:not(.closed) svg:first-child, .nav-icon.closed svg:nth-child(2) {
  display: none;
}

.nav-item-container {
  margin: auto;
}

.nav-item {
  margin-top: 2rem;
}

.nav-item div {
  grid-template-columns: 50% 50%;
  margin-top: 1rem;
  display: grid;
}

.nav-item-title:after {
  border-bottom: 1px solid var(--clr-neutral-900);
  content: "";
  width: 100%;
  display: block;
}

.three-container {
  width: 100%;
}

@media (min-width: 860px) {
  .nav {
    width: 38%;
    position: relative;
  }

  .nav.closed {
    margin-left: 0;
  }

  .nav-icon {
    display: none;
  }

  .three-container {
    width: 62%;
  }
}

